.page__header {
  display: flex;
  justify-content: space-around;
}

.header__title {
  margin-top: 30px;
  font-family: 'Antonio', sans-serif;
  font-weight: 700;
  font-size: 2.5em;
  color: #f3edec;
  text-shadow: 0px 1px rgba(245, 245, 245, 0.308);
  @media (min-width: 768px) {
    font-size: 45px;
  }
}
